var packageType = [
    { value: 1, label: '' },
    { value: 2, label: '' },
    { value: 3, label: '' },
    { value: 6, label: '' },
    { value: 7, label: 'Egs' },
    { value: 10, label: '' },
    { value: 11, label: 'GameZq' },
];

function mergeArr() {
    return Array.prototype.concat.apply([], arguments);
}

export default {
    packageType: packageType,
    packageTypes: mergeArr([{ value: 0, label: '' }], packageType),
    findPackageName(val) {
        var name = '';
        for (var i = 0; i < packageType.length; i++) {
            if(packageType[i].value === val) {
                name = packageType[i].label;
                break;
            }
        }
        return name;
    },
    forceType: [
        { value: 0, label: '' },
        { value: 1, label: '' },
        { value: 2, label: '' }
    ],
    urgencyType: [
        {value: 0, label: ''},
        {value: 1, label: ''}
    ],
    defaultpath: ['[DESKTOP]', '[ROOT]', '[WINDOWS]', '[CUR]'],
    noticeType: [
        { value: 1, label: 'LOL', types: [1, 2] }, // types: 1- 2-
        { value: 2, label: '', types: [2] },
        { value: 3, label: '', types: [2] },
        { value: 4, label: 'PUBG', types: [2] },
        { value: 5, label: '', types: [2] }
    ]
};



